@import url(https://fonts.googleapis.com/css2?family=Noto+Serif+SC&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
}

@media (max-width: 500px) {  
  .App {
    padding-top: 30px;
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.content {
  display: flex;
  width: 100%;
  max-width: 900px;
}

@media (max-width: 500px) {  
  .content {
    flex-direction: column;
  }
}

.nav {
  min-width: 150px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  padding: 0 0 20px 0;
}

.nav p:first-child {
  margin-top: 0;
}

.nav-lesson, .nav-entry {
  cursor: pointer;
  color: #5a5a5a;
}

.nav-lesson, .nav-entry {
  margin: 0.5em 0;
}

.nav-entry {
  margin-left: 0.5em;
}

.nav-entry:hover, .nav-entry.active, .nav-lesson:hover, .nav-lesson.active {
  color: black;
}

.nav-entry.active, .nav-lesson.active {
  font-weight: 500;
}

.title {
  margin: 0;
  line-height: 1.1em;
  margin-bottom: calc(15px + 0.3em); 
}

.audio {
  margin-top: 30px;
  width: 100%;
}

.text {
  flex-grow: 1;
}

.table {
  display: table;
  margin-top: -0.3em;
}

.row {
  display: table-row;
}

.cell {
  display: table-cell;
  padding: 0.0 0 0.2em 0;
}

.table:first-child > .row:first-child {
  /*transform: translateY(-0.5em);*/
}

.dialog-head {
  white-space: nowrap;
}

.chinese {
  /*font-family: 'Noto Serif SC', serif; */
  font-family: "PingFang SC", serif;
  font-size: 1.5em;
  -webkit-font-smoothing: antialiased;
  font-weight: 400;
  line-height: 175%;
  letter-spacing: 0.1em;
  white-space: pre-wrap;
  line-break: auto;
}

